<template>
<!-- v-show="isShow_jcjb" -->
    <div id="cs-jcjb-dialog__wrapper" class="el-dialog__wrapper cs-jcjb-dialog__wrapper"  >
        <div class="cs-jcjb-dailog" v-bind:style="dialogStyle">
            <div class="el-dialog__header"><span class="el-dialog__title">确认解除情报</span>
        </div>
        <div class="el-dialog__body cs-jcjb-dailog__body" >
            <div class="cs-jcjb-box__message">是否确认解除警报？</div>

            <el-form :rules="rules" ref="ruleForm" :model="myformData" >
                <el-form-item prop="type">
                    <el-radio-group v-model="myformData.type">
                        <el-radio :label="1">真实警情</el-radio>
                        <el-radio :label="2">无效警情</el-radio>
                        <el-radio :label="3">重复警情</el-radio>
                    </el-radio-group>
                </el-form-item>

                <el-form-item prop="reback">
                    <el-input type="textarea" v-model="myformData.reback"></el-input>
                </el-form-item>
            </el-form>

        </div>
        <div class="el-dialog__footer cs-jcjb-dailog__footer">
            <div class="dialog-footer">
                <button type="button" class="el-button el-button--default" @click="hideModel"><span>取消</span></button>
                <button type="button" class="el-button el-button--default" @click="makeSure"><span>确认解除</span></button>
            </div>
        </div>
    </div>
</div>


</template>

<script>
export default {
    props: {
        mapHeight:{
            type:Number,
            default:600
        },
        isShow_jcjb :{
            type:Boolean,
            default:false
        },
        jcjb_alertIds:{
            type:String,
            default:""
        }, 
        jcjb_alertCategory:{
            type:(String,Number),
            default:""
        },
        jcjb_remark:{
            type:String,
            default:""
        }
    },
    close: function() {
        console.log("myclose");
    },
    data: function () {
        return {
            rules: {
                reback: [
                    { required: true, message: '请给出回复', trigger: 'blur' }
                ]
            },
            dialogStyle:{
                width:'28%'

            },
            myformData:{
                type:1,
                reback:""
            }

        }
    },
    methods: {
        hideModel(){
            const Bus = global.eventHub;
            Bus.$emit('doto_hide_jcjb');
        },
        saveData() {
            const Bus = global.eventHub;
            let that=this;
            that.$http.post(global.config.weburl+'api/alert/releaseAlert.do', {
                token:global.data.getToken(),
                alertIds:that.jcjb_alertIds,
                alertCategory:that.myformData.type,
                remark:that.myformData.reback
            }).then(function(res){
                var code=res.data.code;
                if(code==0){
                    Bus.$emit('doto_hide_jcjb');
                    Bus.$emit('doto_hide_bjxq');
                    Bus.$emit('doto_jqlb_refresh');
                } else {
                    global.tools.handleError(res.data);
                }
            });
        },
        makeSure() {
            const Bus = global.eventHub;
            let that=this;
            if (that.myformData.type != 1) {
                this.saveData();
                return false;
            }
            that.$refs["ruleForm"].validate((valid) => {
                if (valid) {
                    this.saveData();
                } else {
                    return false;
                }
            })
        }
    },
    created: function () {
        //this.dialogMainHeight = document.documentElement.offsetHeight - 200;
        //console.log("222222"+this.jcjb_alertIds);
        //console.log("222222"+this.jcjb_alertCategory);
        //console.log("222222"+this.jcjb_remark);


    },
    mounted:function() {

    }

}
</script>
<style>
.cs-jcjb-box__message{text-align: center;font-size: 16px;padding: 10px;}
.cs-jcjb-dialog__wrapper{z-index: 2222;}
.cs-jcjb-dailog{
    top:108px;
    right: 40%;
    position: absolute;
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 1px 3px rgba(0,0,0,.3);
    box-sizing: border-box;
}
.cs-jcjb-dailog__footer{
    bottom: 0px;
    position: absolute;
    right: 20px;
}
.cs-jcjb-dailog__body{
    margin-bottom: 60px;
    overflow-x: hidden;
    overflow-y:auto
}
</style>


